import React, { useState } from "react";
import { Div2Children, Example1Con } from "../styledComponents";
import TextField from "@mui/material/TextField";
import PasswordInput from "../../inputs/PasswordInput";
import Button from "@mui/material/Button";
import { Center } from "../../styledComponents";
import {
  inputObjStateChange,
  objStateChange,
} from "../../../commont tools/other";
import { signInFunc, signUpFunc } from "../../../functions/auth";
import { useDispatch } from "react-redux";
import { beautyDate } from "../../../commont tools/beauty";
import Select1 from "../../selects/Select1";
import { objNotEmpty } from "../../../commont tools/obj";

export default function AuthExample1({ setIndex }) {
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    email: "",
    password: "",
    date: beautyDate(new Date()),
    gender: "",
    fName: "",
    lName: "",
  });

  const [error, setError] = useState("");

  const genders = [
    {
      value: 4,
      label: "Not to choose",
    },
    {
      value: 1,
      label: "Male",
    },
    {
      value: 2,
      label: "Female",
    },
    {
      value: 3,
      label: "Dinosaur",
    },
  ];
  console.log(form);
  const signUp = async () => {
    if (objNotEmpty(form)) {
      const { error } = await signUpFunc(dispatch, form);
      if (error) setError(error?.sqlMessage || error);
    }
  };
  return (
    <>
      <p className="mb-2" style={{ color: "var(--red)" }}>
        {error}
      </p>
      <Div2Children>
        <TextField
          id="outlined-basic"
          label="Email"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={form.email}
          onChange={(e) => inputObjStateChange(e, "email", setForm)}
        />
        <PasswordInput
          className="my-2"
          value={form.password}
          onChange={(e) => inputObjStateChange(e, "password", setForm)}
        />
      </Div2Children>
      <Div2Children>
        <TextField
          id="outlined-basic"
          label="First name"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={form.fName}
          onChange={(e) => inputObjStateChange(e, "fName", setForm)}
        />
        <TextField
          id="outlined-basic"
          label="Last name"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={form.lName}
          onChange={(e) => inputObjStateChange(e, "lName", setForm)}
        />
      </Div2Children>
      <Div2Children>
        <TextField
          id="outlined-basic"
          type="date"
          label="Email"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={form.date}
          onChange={(e) => inputObjStateChange(e, "date", setForm)}
        />
        <Select1
          current={form.gender}
          items={genders}
          onChange={(e) => inputObjStateChange(e, "gender", setForm)}
          size="small"
          className="my-2"
        />
      </Div2Children>
      <span className="mb-2 pointer block" onClick={() => setIndex(0)}>
        Already have an account?
      </span>
      <Center>
        <Button variant="contained" onClick={signUp}>
          Sign up
        </Button>
      </Center>
    </>
  );
}
