import React from "react";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";

export default function Loading({ height = "auto", width, style = {} }) {
  return (
    <div
      className="center"
      style={{
        height: height + "px",
        width: width ? width + "px" : "100%",
        ...style,
      }}
    >
      <Box sx={{ display: "flex" }}>
        <CircularProgress />
      </Box>
    </div>
  );
}
