import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import ImgLoading from '../../imgs/ImgLoading';
import { AlignCenter, FlexColumns, MUIButton, NotificationProfileImg, noUnderlineWhite } from '../../styledComponents';

export default function PostComment({ item, collisionRef }) {
  return (
    <div>
      <AlignCenter>
        <ImgLoading
          loadingHeight="30"
          loadingWidth="30"
          Element={NotificationProfileImg}
          src={'/images/other/comment.png'}
        />
        <p>
          <span style={{ fontWeight: 700 }}>{item.username}</span> commented on your post
          <p
            className="mt-1"
            style={{
              color: '#b6b6b6',
              fontSize: 13,
            }}
          >
            {item.value}
          </p>
          <MUIButton className="mt-1 p-1" onClick={() => collisionRef.current.click()}>
            <Link style={noUnderlineWhite} to={'/post/' + item.valueUid}>
              View post
            </Link>
          </MUIButton>
        </p>
      </AlignCenter>
    </div>
  );
}
