import React, { useEffect, useState, useRef } from "react";
import Modal from "./SimpleModal";
import { useSelector } from "react-redux";
import { PushRight } from "./styledComponents";
import { toBase64 } from "../commont tools/img";
import DeleteIcon from "@mui/icons-material/Delete";
import styled from "styled-components";
import { Button } from "@material-ui/core";
import ImgLoading from "./imgs/ImgLoading";
import {
  deleteUserAlbumImage,
  getUserAlbumImagesFunc,
  uploadImageInUserAlbumFunc,
} from "../axios/album";

const AlbumImg = styled.img`
  width: 100%;
  height: 100px;
  object-fit: cover;
  filter: grayscale(0.4);
  transition: 0.3s;

  &:hover {
    cursor: pointer;
    filter: grayscale(0);
  }
`;

const AlbumImgsContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(114px, 1fr));
  gap: 10px;
`;

export default function Album({ open, setOpen, currentImage, onChooseImage }) {
  const { user } = useSelector((state) => state);

  const [images, setImages] = useState([]);
  const [uploadImages, setUploadImages] = useState([]);
  const fileLabel = useRef(null);

  // Image modal
  {
    var [imgModalOpen, setImgModalOpen] = useState(false);

    var imgModalOnSave = async () => {
      const formData = new FormData();
      for (let k of uploadImages) {
        formData.append("images", k.file);
      }
      const {
        data: { data, error },
      } = await uploadImageInUserAlbumFunc(formData);
      if (error) alert(error);
      else if (data) {
        setImgModalOpen(false);
        setImages(data);
      }
    };
  }
  {
    var [deleteOpen, setDeleteOpen] = useState(false);
    var [deleteUid, setDeleteUid] = useState(null);

    var deleteOnSave = async () => {
      const {
        data: { data },
      } = await deleteUserAlbumImage(images[deleteUid]);
      if (data) setImages(data);
      setDeleteOpen(false);
    };
  }

  const fetchMyImages = async () => {
    const {
      data: { data, error },
    } = await getUserAlbumImagesFunc();
    if (error) alert(error);
    else {
      setImages(data);
    }
  };
  useEffect(() => {
    fetchMyImages();
  }, []);
  return (
    <Modal
      open={open}
      setOpen={setOpen}
      successButton=""
      header="Your album"
      maxWidth="md"
      noBtns
    >
      <Modal
        open={imgModalOpen}
        setOpen={setImgModalOpen}
        successButton="Upload"
        onSave={imgModalOnSave}
        header="Uploaded images"
      >
        <AlbumImgsContainer>
          {uploadImages
            .map((a) => a.img)
            .map((a, b) => (
              <div className="relative" key={a + b}>
                <DeleteIcon
                  style={{
                    position: "absolute",
                    top: "3px",
                    right: "4px",
                    cursor: "pointer",
                    background: "white",
                    boxShadow: "rgb(0 0 0 / 32%) 0px 0px 4px",
                    borderRadius: "5px",
                    padding: "3px",
                    zIndex: "1",
                  }}
                  className="color-red"
                  onClick={() => {
                    const newUploadImages = [...uploadImages].filter(
                      (c, d) => d != b
                    );
                    setUploadImages(newUploadImages);
                  }}
                />
                <AlbumImg key={a} src={a} />
              </div>
            ))}
        </AlbumImgsContainer>
      </Modal>
      <Modal
        open={deleteOpen}
        setOpen={setDeleteOpen}
        header="Delete image"
        onSave={deleteOnSave}
      >
        <AlbumImgsContainer>
          <ImgLoading
            src={"/images/user/" + user.uid + "/" + images[deleteUid]}
            Element={AlbumImg}
          />
        </AlbumImgsContainer>
      </Modal>
      <div className="row">
        <label htmlFor="file" ref={fileLabel}></label>
        <input
          id="file"
          type="file"
          multiple
          onChange={async (e) => {
            const newArr = [];
            for (let k of [...e.target.files]) {
              newArr.push({ file: k, img: await toBase64(k) });
            }
            setImgModalOpen(true);
            setUploadImages(newArr);
          }}
          hidden
        />
        <PushRight>
          <Button
            type="button"
            variant="contained"
            onClick={() => {
              fileLabel.current.click();
            }}
            style={{ backgroundColor: "var(--blue)", color: "white" }}
          >
            Upload image
          </Button>
        </PushRight>
        <AlbumImgsContainer>
          {images.map((a, b) => (
            <div className="relative" key={a + b}>
              <DeleteIcon
                style={{
                  position: "absolute",
                  top: "3px",
                  right: "4px",
                  cursor: "pointer",
                  background: "white",
                  boxShadow: "rgb(0 0 0 / 32%) 0px 0px 4px",
                  borderRadius: "5px",
                  padding: "3px",
                  zIndex: "1",
                }}
                className="color-red"
                onClick={() => {
                  setDeleteOpen(true);
                  setDeleteUid(b);
                }}
              />
              <AlbumImg
                key={a}
                onClick={() => {
                  setOpen(false);
                  onChooseImage(a);
                }}
                src={"/images/user/" + user.uid + "/" + a}
              />
            </div>
          ))}
        </AlbumImgsContainer>
      </div>
    </Modal>
  );
}
