import React, { useState } from "react";
import { queryParams } from "../../../commont tools/url";
import { Flex } from "../../../components/styledComponents";
import Categories from "./Categories";
import Users from "./Users";
import Posts from "./Posts";

export default function Index(props) {
  const { category, page: pageValue = 1 } = queryParams();
  const [items, setItems] = useState([]);
  const [pages, setPages] = useState([]);
  const [page, setPage] = useState(+pageValue);

  const itemsObj = {
    items,
    setItems,
  };
  const pagesObj = {
    pages,
    setPages,
  };
  const pageObj = {
    page,
    setPage,
  };

  const itemsAndPagesObj = {
    ...itemsObj,
    ...pagesObj,
    ...pageObj,
  };
  return (
    <Flex style={{ gap: 20 }}>
      <Categories props={props} obj={itemsAndPagesObj} />
      {
        {
          user: <Users obj={itemsAndPagesObj} />,
          post: <Posts obj={itemsAndPagesObj} />,
        }[category]
      }
    </Flex>
  );
}
