import styled from 'styled-components';

export const ChatDiv = styled.div`
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 10px;
  z-index: 99;
  display: flex;
  gap: 10px;
  align-items: flex-end;
  max-width: 100vw;
  overflow-x: auto;
`;

export const Chat = styled.div`
  background: white;
  height: 100%;
  box-shadow: 0 0 4px #00000057;
  border-radius: 5px;
  min-width: 310px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;

export const ChatName = styled.div`
  background-color: var(--blue);
  color: white;
`;

export const ChatMesssages = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
  overflow-y: auto;
  height: 300px;
  max-width: 310px;
`;

export const ChatMessage = styled.div`
  padding: 4px 8px;
  border-radius: 14px;
  background: #f0f0f0;
  display: inline-block;
`;

export const ChatImage = styled.img`
  height: 35px;
  width: 35px;
  object-fit: conver;
  border-radius: 50%;
  margin-right: 5px;
`;
