import React from 'react';
import { deleteRoomAxios } from '../../axios/chat';

import SimpleModal from '../SimpleModal';

export default function DeleteRoomModal({
  data,
  open,
  setOpen,
  type,
  onSave: onSaveFunc,
  defaultValue = () => {},
  ...props
}) {
  const onSave = async () => {
    const { uid } = data;
    await deleteRoomAxios(uid);
    onSaveFunc();
    setOpen(false);
  };

  return (
    <SimpleModal open={open} setOpen={setOpen} onSave={onSave} {...props}>
      Are you sure you want to delete room {data.roomName}
    </SimpleModal>
  );
}
