import styled from 'styled-components';

export const FullScreenCenter = styled.div`
  flex: 1;
  display: grid;
  place-items: center;
`;

export const Center = styled.div`
  display: flex;
  place-items: center;
`;
export const FlexColumns = styled.div`
  display: flex;
  flex-direction: column;
`;

export const Flex = styled.div`
  display: flex;
`;

export const AlignCenter = styled.div`
  display: flex;
  align-items: center;
`;

export const JustifySpaceBetween = styled.div`
  display: flex;
  justify-content: space-between;
`;

export const JustifySpaceBetweenAlignCenter = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

export const PushRight = styled.div`
  display: flex;
  justify-content: flex-end;
  padding: 10px 0;
`;

export const noUnderlineBlack = {
  color: 'black',
  textDecoration: 'none',
};

export const noUnderlineWhite = {
  color: 'white',
  textDecoration: 'none',
};

export const ThreeDotMenuP = styled.p`
  cursor: pointer;
  padding: 10px 20px;
  transition: 0.3s;

  &:hover {
    background: #f6f6f6;
  }
`;

export const MUIButton = styled.button`
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
  outline: 0;
  border: 0;
  margin: 0;
  border-radius: 0;
  padding: 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  -moz-appearance: none;
  -webkit-appearance: none;
  -webkit-text-decoration: none;
  text-decoration: none;
  color: inherit;
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  min-width: 64px;
  padding: 6px 16px;
  border-radius: 4px;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  color: #fff;
  background-color: #1976d2;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
`;

export const NavbarChat = styled.div`
  position: absolute;
  padding: 10px;
  background: white;
  right: 0;
  width: 350px;
  border-radius: 5px;
  top: 100%;
  box-shadow: 0 0 5px #0000004d;
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 432px;
  overflow-y: auto;
`;

export const FullscreenTransparent = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9;
`;

export const NotificationProfileImg = styled.img`
  height: 35px;
  width: 35px;
  border-radius: 50%;
  object-fit: cover;
`;
