import React, { useEffect, useState, useRef } from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
import { Redirect } from 'react-router';
import { Link, useHistory } from 'react-router-dom';
import {
  AlignCenter,
  Flex,
  FlexColumns,
  FullscreenTransparent,
  JustifySpaceBetweenAlignCenter,
  MUIButton,
  NavbarChat,
  NotificationProfileImg,
  noUnderlineBlack,
  noUnderlineWhite,
} from './styledComponents';
import { queryParams } from '../commont tools/url';
import { useDispatch, useSelector } from 'react-redux';
import ThreeDotMenu from './ThreeDotMenu';
import { logoutFunc } from '../functions/auth';
import ForumIcon from '@mui/icons-material/Forum';
import Badge from '@mui/material/Badge';
import { getChatAndNotificationsAxios } from '../axios/chat';
import { addChatFunc, updateNavbarMessages } from '../functions/chat';
import Moment from 'react-moment';
import NotificationsIcon from '@mui/icons-material/Notifications';
import { getMyNotificationsAxios, seenAllNotificationsAxios } from '../axios/notifications';
import SimpleCollision from './collision/SimpleCollision';
import ImgLoading from './imgs/ImgLoading';
import {
  addFriendByUidAxios,
  cancelRecieveRequestByUidAxios,
  cancelRequestAxios,
  cancelRequestByUidAxios,
} from '../axios/users';
import RequestFrom from './navbar/notifications/RequestFrom';
import RequestTo from './navbar/notifications/RequestTo';
import RoomModal from './chat/RoomModal';
import PostComment from './navbar/notifications/PostComment';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

export default function SearchAppBar() {
  let history = useHistory();
  const { category = 'user', value } = queryParams();
  const [search, setSearch] = React.useState(value);
  const { user, chats, navbarMessages } = useSelector((state) => state);
  const dispatch = useDispatch();
  const [notifications, setNotifications] = useState([]);

  const defaultModalData = { name: '', users: [] };
  const [roomModalOpen, setRoomModalOpen] = useState(false);
  const [roomModalData, setRoomModalData] = useState(defaultModalData);
  const [roomModalType, setRoomModalType] = useState(defaultModalData);

  const getChatData = async () => {
    const {
      data: { data },
    } = await getChatAndNotificationsAxios();
    if (data) updateNavbarMessages(dispatch, data);
  };

  const getNotifications = async () => {
    const {
      data: { data },
    } = await getMyNotificationsAxios();
    if (data) setNotifications(data);
  };

  useEffect(() => {
    getChatData();
    getNotifications();
  }, []);

  const notSeened = navbarMessages.reduce((a, b) => a + { 0: 1, 1: 0 }[b.seen], 0);

  const collisionRef = useRef();
  const notSeenedNotifications = notifications.reduce((a, b) => a + (b.seen ? 0 : 1), 0);
  return (
    <Box className="mb-3">
      <AppBar position="static" style={{ backgroundColor: 'var(--blue)' }}>
        <Toolbar>
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}>
            <Link to="/" style={noUnderlineWhite}>
              Facebook
            </Link>
          </Typography>
          <AlignCenter style={{ gap: 10 }}>
            {user && (
              <Link style={noUnderlineWhite} to={'/profile/' + user.uid}>
                {user.fName}
              </Link>
            )}
            <SimpleCollision
              onOpen={() => {
                seenAllNotificationsAxios();
              }}
              ref={collisionRef}
              header={
                <Badge badgeContent={String(notSeenedNotifications)} color="error">
                  <NotificationsIcon style={{ color: 'white' }} />
                </Badge>
              }
              content={
                <>
                  {!notifications.length && 'No notifications'}
                  {notifications.map((a) => {
                    return (
                      <div key={a.uid}>
                        {
                          {
                            requestFrom: <RequestFrom setNotifications={setNotifications} item={a} />,
                            requestTo: <RequestTo setNotifications={setNotifications} item={a} />,
                            postComment: <PostComment item={a} collisionRef={collisionRef} />,
                          }[a.type]
                        }
                      </div>
                    );
                  })}
                </>
              }
            />
            <SimpleCollision
              header={
                <Badge badgeContent={String(notSeened)} color="error">
                  <ForumIcon style={{ color: 'white' }} />
                </Badge>
              }
              content={
                <>
                  <MUIButton
                    onClick={() => {
                      setRoomModalOpen(true);
                      setRoomModalData(defaultModalData);
                      setRoomModalType('create');
                    }}
                  >
                    Create room
                  </MUIButton>
                  {!navbarMessages.length && 'No messages'}
                  {navbarMessages.map((a, b) => {
                    let name, message, messageSender, date;
                    if (a.name) name = a.name;
                    else if (a.message) name = a.message.name;

                    if (a.message) {
                      message = a.message.message;
                      date = a.message.date;
                      messageSender = a.message.name;
                    }
                    return (
                      <p
                        onClick={() => addChatFunc(dispatch, chats, a.uid)}
                        key={a.uid}
                        className="pointer"
                        style={{
                          background: '#f8f8f8',
                          padding: '5px',
                          borderRadius: '5px',
                        }}
                      >
                        <p style={{ fontSize: 20 }}>{name}</p>
                        {a.message ? (
                          <JustifySpaceBetweenAlignCenter>
                            <div style={{ color: a.seen ? 'black' : 'var(--blue)' }}>
                              {messageSender}: <span style={{ color: '#c8c8c8' }}>{message}</span>
                            </div>
                            {date && (
                              <div>
                                <Moment date={new Date(date)} durationFromNow /> Ago
                              </div>
                            )}
                          </JustifySpaceBetweenAlignCenter>
                        ) : (
                          'No messages'
                        )}
                      </p>
                    );
                  })}
                </>
              }
            />
            <RoomModal
              open={roomModalOpen}
              setOpen={setRoomModalOpen}
              data={roomModalData}
              setData={setRoomModalData}
              type={roomModalType}
              onSave={getChatData}
            />
            <Search>
              <SearchIconWrapper>
                <SearchIcon style={{ color: 'white' }} />
              </SearchIconWrapper>
              <StyledInputBase
                style={{ color: 'white' }}
                placeholder="Search…"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                inputProps={{ 'aria-label': 'search' }}
                onKeyDown={(e) => {
                  if (e.keyCode == 13 && search) history.push(`/search?value=${search}&category=${category}`);
                }}
              />
            </Search>
            <ThreeDotMenu
              items={[
                {
                  title: (
                    <Link to="/settings" style={{ ...noUnderlineBlack, padding: 0 }}>
                      Settings
                    </Link>
                  ),
                },
                { title: 'Logout', onClick: () => logoutFunc(dispatch) },
              ]}
              style={{ color: 'white' }}
            />
          </AlignCenter>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
