import { TextField } from "@mui/material";
import React, { useEffect, useState } from "react";
import SimpleModal from "../SimpleModal";
import {
  createCommentFunc,
  deleteCommentFunc,
  getCommentsFunc,
} from "../../axios/post";
import PostComment from "./PostComment";
import { CommentInputBtnCont } from "./styledComponent";
import { Button } from "@material-ui/core";
import { inputObjStateChange } from "../../commont tools/other";
import { nest, setChildrenItemsToParent } from "../../commont tools/tree";
import DeleteModal from "./DeleteModal";

export default function CommentsModal({
  commentsModalObj: {
    commentsModalOpen,
    setCommentsModalOpen,
    uid: postUid,
    userUid,
  },
}) {
  const defaultData = {
    title: "",
    commentUid: null,
    userUid: null,
    postUid,
  };

  const setCommentsFunc = (data) => setComments(nest(data, "commentUid"));

  const [comments, setComments] = useState([]);
  const [data, setData] = useState(defaultData);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [deleteModalData, seteleteModalData] = useState(null);
  const deleteModalOnSave = async () => {
    const {
      data: { data },
    } = await deleteCommentFunc(deleteModalData);
    if (data) setCommentsFunc(data);
    setDeleteModalOpen(false);
  };

  const deleteModalObj = {
    deleteModalOnSave,
    deleteModalOpen,
    setDeleteModalOpen,
  };

  useEffect(async () => {
    const {
      data: { data },
    } = await getCommentsFunc(postUid);
    setCommentsFunc(data);
  }, []);

  const addComment = async () => {
    const { title } = data;
    if (!title) return;
    const {
      data: { data: dataa },
    } = await createCommentFunc(data);
    if (dataa) setCommentsFunc(dataa);
    setData(defaultData);
  };
  const CommentTree = (items = comments) => (
    <>
      {items.map((a) => (
        <div key={a.uid}>
          <PostComment
            setData={setData}
            data={data}
            comment={a}
            setDeleteModalOpen={setDeleteModalOpen}
            seteleteModalData={seteleteModalData}
            userUid={userUid}
          />
          <div style={{ paddingLeft: 40 }}>
            {a.items.length ? <>{CommentTree(a.items)} </> : ""}
          </div>
        </div>
      ))}
    </>
  );

  return (
    <SimpleModal
      header="Comments"
      maxWidth="md"
      open={commentsModalOpen}
      setOpen={setCommentsModalOpen}
      noBtns
    >
      <CommentInputBtnCont>
        <TextField
          id="outlined-basic"
          label="Add comment"
          variant="outlined"
          size="small"
          fullWidth
          value={data.title}
          onChange={(e) => inputObjStateChange(e, "title", setData)}
        />
        <Button
          size="small"
          variant="contained"
          onClick={addComment}
          style={{ backgroundColor: "var(--blue)", color: "white" }}
        >
          Add Comment
        </Button>
      </CommentInputBtnCont>
      {CommentTree()}
      <DeleteModal deleteModalObj={deleteModalObj} />
    </SimpleModal>
  );
}
