import axios from 'axios';

export const getChatMessagesAxios = (uid, { chat = '1', messages = '1' } = {}) =>
  axios.get('/chatmessages/' + uid + `?chat=${chat}&messages=${messages}`);

export const sendChatMessageAxios = (uid, { message, notification = 0 }) =>
  axios.post('/chatmessage/' + uid, { message, notification });

export const seenChatAxios = (uid) => axios.put('/seenchat/' + uid);

export const getPrivateConnectionAxios = (uid) => axios.get('/privateconnection/' + uid);

export const getChatAndNotificationsAxios = () => axios.get('/chatsandnotifications');

export const createRoomAxios = (data) => axios.post('/room', data);

export const updateRoomAxios = (uid, data) => axios.put('/room/' + uid, data);

export const deleteRoomAxios = (uid) => axios.delete('/room/' + uid);
