import { getPrivateConnectionAxios } from '../axios/chat';

export const addChatFunc = (dispatch, chats, uid, data) => {
  if (chats.find((a) => a.uid == uid)) return;
  const payload = { open: true, uid };
  if (data) payload.data = data;
  dispatch({
    type: 'ADD_CHAT',
    payload,
  });
};

export const addPrivateChatFunc = async (uid, dispatch, chats, name) => {
  uid = +uid;
  const {
    data: { data },
  } = await getPrivateConnectionAxios(uid);
  console.log(data);
  addChatFunc(dispatch, chats, data, name);
};

export const removeChats = (dispatch) => dispatch({ type: 'REMOVE_CHATS' });

export const closeChatFunc = (dispatch, uid) => dispatch({ type: 'CLOSE_CHAT', payload: uid });

export const minimizeChatFunc = (dispatch, uid) => dispatch({ type: 'MINIMIZE_CHAT', payload: uid });

export const updateNavbarMessages = (dispatch, payload) => dispatch({ type: 'UPDATE_NAVBAR_MESSAGES', payload });
