import styled from "styled-components";

export const ImgContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(114px, 1fr));
  gap: 10px;
`;

export const Img = styled.img`
  width: 100%;
  height: 100px;
  object-fit: cover;
`;

export const deleteIconStyle = {
  position: "absolute",
  color: "var(--red)",
  top: "2px",
  right: "2px",
  cursor: "pointer",
  background: "white",
  boxShadow: "0 0 4px #00000052",
  borderRadius: "5px",
  padding: "3px",
};

export const moveIconStyle = {
  position: "absolute",
  top: "30px",
  right: "2px",
  cursor: "pointer",
  color: "var(--blue)",
  background: "white",
  boxShadow: "0 0 4px #00000052",
  borderRadius: "5px",
  padding: "3px",
};

export const SinglePostContainer = styled.div`
  padding: 25px;
  box-shadow: 0 0 4px #00000021;
  border-radius: 5px;
  margin-bottom: 20px;
`;

export const SinglePostProfileImg = styled.img`
  height: 60px;
  width: 60px;
  object-fit: cover;
  border-radius: 50%;
`;

export const SinglePostUsername = styled.p`
  font-weight: bold;
  font-size: 18px;
`;

export const SinglePostDate = styled.p`
  font-size: 14px;
  color: #b2b2b2;
`;

export const SinglePostImgContainer = styled.div`
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
  align-items: center;
`;

export const SinglePostImg = styled.img`
  width: 100%;
  max-height: 200px;
  object-fit: contain;
`;

export const SinglePostDescription = styled.pre`
  padding: 20px;
`;

export const CommentMainDiv = styled.div`
  border-radius: 10px;
  padding: 0px 12px;
`;

export const CommentDiv = styled.div`
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr auto auto auto;
  align-items: center;
  gap: 10px;
`;

export const CommentImg = styled.img`
  height: 40px;
  width: 40px;
  object-fit: cover;
  border-radius: 50%;
`;

export const CommentInputBtnCont = styled.div`
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 10px;
  margin-bottom: 10px;
`;
