import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import ImgLoading from '../imgs/ImgLoading';
import { Center, FlexColumns, JustifySpaceBetween, noUnderlineBlack } from '../styledComponents';
import CommentsModal from './CommentsModal';
import {
  SinglePostContainer,
  SinglePostDate,
  SinglePostDescription,
  SinglePostImg,
  SinglePostImgContainer,
  SinglePostProfileImg,
  SinglePostUsername,
} from './styledComponent';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import IconButton from '@mui/material/IconButton';
import Badge from '@mui/material/Badge';
import { hasPostLikedAxios, likePostAxios } from '../../axios/post';

export default function SinglePost({
  post: { image, username, date, userUid, images, description, uid, likes = 0 },
  icons = [],
}) {
  const [commentsModalOpen, setCommentsModalOpen] = useState(false);
  const [commentsModalData, setCommentsModalData] = useState({});
  const [liked, setLiked] = useState(0);
  const [alllikes, setAlllikes] = useState(0);

  const commentsModalObj = {
    commentsModalOpen,
    setCommentsModalOpen,
    commentsModalData,
    setCommentsModalData,
    uid,
    userUid,
  };

  const { uid: pageUid } = useParams();

  const hasPostLiked = async () => {
    const {
      data: { data },
    } = await hasPostLikedAxios(uid);
    if (data) {
      setLiked(data ? 1 : 0);
      setAlllikes(data ? likes - 1 : likes);
    } else setAlllikes(likes);
  };

  useEffect(() => {
    setCommentsModalOpen(false);
  }, [pageUid]);

  useEffect(() => {
    hasPostLiked();
  }, [likes]);

  const likePost = async () => {
    setLiked(liked ? 0 : 1);
    likePostAxios(uid);
  };

  return (
    <SinglePostContainer>
      <JustifySpaceBetween>
        <Center style={{ gap: 10 }}>
          <ImgLoading noImg="/images/other/noProfileImg.png" Element={SinglePostProfileImg} src={image} />
          <FlexColumns>
            <SinglePostUsername>
              <Link to={'/profile/' + userUid} style={noUnderlineBlack}>
                {username}
              </Link>
            </SinglePostUsername>
            <SinglePostDate>{date.slice(0, 10)}</SinglePostDate>
          </FlexColumns>
        </Center>
        <div>
          {icons.map(({ icon, onClick = () => {} }) => {
            return (
              <span className="pointer" onClick={onClick}>
                {icon}
              </span>
            );
          })}
        </div>
      </JustifySpaceBetween>
      <SinglePostDescription>{description}</SinglePostDescription>
      <SinglePostImgContainer>
        {images.map((a) => (
          <ImgLoading key={a.uid} Element={SinglePostImg} src={`/images/user/${a.url}`} />
        ))}
      </SinglePostImgContainer>
      <p className="color-blue pointer mt-3" onClick={() => setCommentsModalOpen(true)}>
        Comments
      </p>
      {commentsModalOpen && <CommentsModal commentsModalObj={commentsModalObj} />}
      <br />
      <IconButton onClick={likePost}>
        <Badge badgeContent={String(alllikes + (liked ? 1 : 0))} color="error">
          <ThumbUpIcon style={{ color: liked ? 'var(--blue)' : '' }} />
        </Badge>
      </IconButton>
    </SinglePostContainer>
  );
}
