import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BrowserRouter as Router, Switch, Route, Link, useHistory } from 'react-router-dom';
import { setUserFunc } from './functions/auth';
import _default from './routes/default';
import onAuth from './routes/onAuth';
import Main from './pages/default/main';
import ProtectedRouteOnAuth from './routeTypes/onAuth';
import NotLogined from './pages/default/main/NotLogined';
import Settings from './pages/onAuth/settings';
import { removeChats } from './functions/chat';

import { Prompt } from 'react-router';
const Tazo = () => {
  const history = useHistory();
  const redirect = () => {
    history.push('/');
  };
  return (
    <div>
      <Prompt when={true} message="You have unsaved changes, are you sure you want to leave?" />
      <Link to="/">tazo</Link>
      <button onClick={redirect}>go to main</button>
    </div>
  );
};

function App() {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state);

  useEffect(() => {
    setUserFunc(dispatch);
  }, []);
  useEffect(() => {
    if (!user) removeChats(dispatch);
  }, [user]);

  return (
    <Router>
      <Switch>
        {user ? (
          <ProtectedRouteOnAuth exact path="/" component={Main} />
        ) : user != false ? (
          <Route exact path="/" component={NotLogined} />
        ) : (
          ''
        )}
        {_default} {onAuth}
      </Switch>
    </Router>
  );
}

export default App;
