import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { getPostFunc } from '../../../axios/post';
import {
  addFriendAxios,
  cancelRequestAxios,
  connectionToUser,
  deleteFriendAxios,
  getUserAxios,
  sendRequestAxios,
} from '../../../axios/users';
import ImgLoading from '../../../components/imgs/ImgLoading';
import { FlexColumns, Flex, JustifySpaceBetweenAlignCenter, AlignCenter } from '../../../components/styledComponents';
import { BgImage, Container, InnerContainer, ProfileImage } from '../user/styledComponent';
import EmailIcon from '@mui/icons-material/Email';
import PersonRemoveIcon from '@mui/icons-material/PersonRemove';
import PersonAddAlt1Icon from '@mui/icons-material/PersonAddAlt1';
import CancelScheduleSendIcon from '@mui/icons-material/CancelScheduleSend';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Button } from '@material-ui/core';
import PostModal from '../../../components/post/PostModal';
import { createPostFunc, deletePostFunc, getPostsFunc, updatePostFunc } from '../../../axios/post';
import SinglePost from '../../../components/post/SinglePost';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import DeleteModal from '../../../components/post/DeleteModal';
import { useParams } from 'react-router';

export default function Index(props) {
  const { uid } = useParams();

  const [post, setPost] = useState();
  const [loaded, setLoaded] = useState(false);
  const { user } = useSelector((state) => state);

  //
  const modalPostDefaultData = {
    description: '',
    images: [],
  };

  const [modalPostOpen, setModalPostOpen] = useState(false);
  const [modalPostType, setModalPostType] = useState('');
  const [modalPostData, setModalPostData] = useState(modalPostDefaultData);

  const modalPostOnSave = async () => {
    const { description, uid, images } = modalPostData;
    if (!description) return;
    if (modalPostType == 'update') {
      const {
        data: { data },
      } = await updatePostFunc(
        uid,
        {
          description,
          images,
        },
        { returnPost: '1' },
      );
      if (data) {
        setPost(data);
      }
    }

    setModalPostOpen(false);
  };

  const modalPostObj = {
    modalPostOpen,
    setModalPostOpen,
    modalPostType,
    setModalPostType,
    modalPostData,
    setModalPostData,
    modalPostOnSave,
  };

  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [deleteModalType, setDeleteModalType] = useState(false);
  const [deleteModalData, setDeleteModalData] = useState({});

  const deleteModalOnSave = async () => {
    deletePostFunc(deleteModalData.uid);
    setPost(undefined);
  };

  const deleteModalObj = {
    deleteModalOpen,
    setDeleteModalOpen,
    deleteModalType,
    setDeleteModalType,
    deleteModalData,
    setDeleteModalData,
    deleteModalOnSave,
  };

  const postPost = async () => {
    const {
      data: { data },
    } = await getPostFunc(uid);
    setLoaded(true);
    if (data) {
      setPost(data);
    }
  };
  //

  useEffect(() => {
    postPost();
  }, [uid]);
  if (!loaded) return 'Loading';
  if (!post) return "Post doesn't exist";
  return (
    <div>
      <SinglePost
        post={{
          ...post,
          username: post.name,
          image: '/images/user/' + post.userUid + '/' + post.profileImage,
        }}
        icons={
          user.uid == post.userUid || user.status == 2
            ? [
                {
                  icon: (
                    <EditIcon
                      style={{
                        color: 'var(--blue)',
                      }}
                    />
                  ),
                  onClick: () => {
                    let { description, images, uid } = post;
                    images = images.map((a) => a.url);
                    setModalPostData({ description, uid, images });
                    setModalPostOpen(true);
                    setModalPostType('update');
                  },
                },
                {
                  icon: (
                    <DeleteIcon
                      style={{ color: 'var(--red)' }}
                      onClick={() => {
                        let { description, uid } = post;
                        setDeleteModalOpen(true);
                        setDeleteModalData({
                          uid,
                          header: description.slice(0, 30),
                        });
                      }}
                    />
                  ),
                },
              ]
            : []
        }
      />
      <PostModal modalPostObj={modalPostObj} />
      <DeleteModal deleteModalObj={deleteModalObj} />
    </div>
  );
}
