import { IconButton } from '@mui/material';
import React, { forwardRef, useEffect, useState } from 'react';
import { FullscreenTransparent, NavbarChat } from '../styledComponents';

function SimpleCollision({ header, content, onOpen = () => {} }, ref) {
  const [open, setOpen] = useState(false);
  useEffect(() => {
    if (!open) return;
    onOpen();
  }, [open]);
  return (
    <div className="relative">
      <IconButton onClick={() => setOpen(!open)}>{header}</IconButton>
      {open && (
        <>
          <FullscreenTransparent ref={ref} onClick={() => setOpen(false)} />
          <NavbarChat>{content}</NavbarChat>
        </>
      )}
    </div>
  );
}

export default forwardRef(SimpleCollision);
