import React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import ListSubheader from "@mui/material/ListSubheader";
import { Link, useHistory } from "react-router-dom";
import {
  Center,
  FlexColumns,
  noUnderlineBlack,
} from "../../../components/styledComponents";
import Pagination from "@mui/material/Pagination";
import { queryParams } from "../../../commont tools/url";
import EmailIcon from "@mui/icons-material/Email";
import CancelScheduleSendIcon from "@mui/icons-material/CancelScheduleSend";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import PersonAddAlt1Icon from "@mui/icons-material/PersonAddAlt1";
import PersonRemoveIcon from "@mui/icons-material/PersonRemove";
import {
  addFriendAxios,
  cancelRequestAxios,
  deleteFriendAxios,
  sendRequestAxios,
} from "../../../axios/users";
import AssignmentIcon from "@mui/icons-material/Assignment";

export default function Users({
  obj: { items, pages, page, setPage, setItems },
}) {
  let history = useHistory();
  const { value, category, page: paramsPage = 1 } = queryParams();
  console.log(items);
  return (
    <FlexColumns style={{ gap: 20, flex: 1 }}>
      <List
        sx={{ width: "100%", boxShadow: "none!important" }}
        style={{ boxShadow: "none!important" }}
        subheader={<ListSubheader>Posts</ListSubheader>}
      >
        {items.map(({ uid, description }, b) => (
          <ListItem key={uid}>
            <Link
              style={{ width: "100%", ...noUnderlineBlack }}
              to={`/post/${uid}`}
            >
              <Center>
                <ListItemIcon style={{ minWidth: 40 }}>
                  <AssignmentIcon />
                </ListItemIcon>
                <ListItemText
                  id="switch-list-label-wifi"
                  primary={description?.slice(0, 40)}
                />
              </Center>
            </Link>
          </ListItem>
        ))}
      </List>
      <Pagination
        count={pages}
        page={page}
        onChange={(e, page) => {
          setPage(page);
          const pageurl = page > 1 ? `&page=${page}` : "";
          history.push(`/search?value=${value}&category=${category}` + pageurl);
        }}
      />
    </FlexColumns>
  );
}
