import axios from 'axios';

export const getUsersAxios = ({ search = '', limit = 10, page = 1, noEmailSearch = '' } = {}) =>
  axios.get(`/users?search=${search}&limit=${limit}&page=${page}&noEmailSearch=${noEmailSearch}`);

export const getUserAxios = (uid) => axios.get('/user/' + uid);

export const sendRequestAxios = (uid) => axios.post('/sendrequest/' + uid);

export const cancelRequestAxios = (uid) => axios.delete('/sendrequest/' + uid);

export const cancelRequestByUidAxios = (uid) => axios.delete('/sendrequestbyuid/' + uid);

export const cancelRecieveRequestAxios = (uid) => axios.delete('/recieverequest/' + uid);

export const cancelRecieveRequestByUidAxios = (uid) => axios.delete('/recieverequestByUid/' + uid);

export const addFriendAxios = (uid) => axios.post('/friend/' + uid);

export const addFriendByUidAxios = (uid) => axios.post('/friendbyuid/' + uid);

export const deleteFriendAxios = (uid) => axios.delete('/friend/' + uid);

export const connectionToUser = (uid) => axios.get('/userconnection/' + uid);

export const updateUserAxios = (uid, data) => axios.put('/user/' + uid, data);

export const getMyFriendsAxios = () => axios.get('/myfriends');
