import styled from "styled-components";

export const Example1Con = styled.div`
  width: 100%;
  padding: 20px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  border-radius: 8px;
  transition: 0.3s;
`;

export const Div2Children = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
`;
