import React from "react";
import { Button, Divider } from "@material-ui/core";
import { TextField } from "@mui/material";
import { useEffect } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { inputObjStateChange } from "../../commont tools/other";
import PasswordInput from "../inputs/PasswordInput";
import Select1 from "../selects/Select1";
import { Center, MUIButton } from "../styledComponents";
import { Div2Children, Example1Con } from "./styledComponents";
import { updateUserAxios } from "../../axios/users";
import { setUserFunc } from "../../functions/auth";

export default function SettingsFields() {
  const { user } = useSelector((state) => state);
  const dispatch = useDispatch();
  const [data, setData] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    const tempUser = { ...user, oldPassword: "" };
    delete tempUser.password;
    tempUser.date = tempUser.date.slice(0, 10);

    setData(tempUser);
  }, [user]);

  const genders = [
    {
      value: 4,
      label: "Not to choose",
    },
    {
      value: 1,
      label: "Male",
    },
    {
      value: 2,
      label: "Female",
    },
    {
      value: 3,
      label: "Dinosaur",
    },
  ];
  const save = async () => {
    if (!data.oldPassword) return setError("Please enter old password");
    const {
      data: { data: updatedUser, error },
    } = await updateUserAxios(user.uid, data);
    if (updatedUser) setUserFunc(dispatch, updatedUser);
    else if (error) setError(error);
  };

  if (!data) return "";
  return (
    <Example1Con style={{ maxWidth: 500 }}>
      <p className="color-red mb-3">{error}</p>
      <Div2Children style={{ gap: "2px 10px" }}>
        <TextField
          id="outlined-basic"
          label="Email"
          variant="outlined"
          type="email"
          size="small"
          fullWidth
          className="my-2"
          value={data.email}
          onChange={(e) =>
            inputObjStateChange(
              e,
              "email",
              setData,
              () => setError(""),
              () => setError("")
            )
          }
        />
        <PasswordInput
          className="my-2"
          value={data.password}
          onChange={(e) =>
            inputObjStateChange(e, "password", setData, () => setError(""))
          }
        />
        <TextField
          id="outlined-basic"
          label="First name"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={data.fName}
          onChange={(e) =>
            inputObjStateChange(e, "fName", setData, () => setError(""))
          }
        />
        <TextField
          id="outlined-basic"
          label="Last name"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={data.lName}
          onChange={(e) =>
            inputObjStateChange(e, "lName", setData, () => setError(""))
          }
        />
        <TextField
          id="outlined-basic"
          type="date"
          label="Date"
          variant="outlined"
          size="small"
          fullWidth
          className="my-2"
          value={data.date}
          onChange={(e) =>
            inputObjStateChange(e, "date", setData, () => setError(""))
          }
        />
        <Select1
          current={data.gender}
          items={genders}
          onChange={(e) =>
            inputObjStateChange(e, "gender", setData, () => setError(""))
          }
          size="small"
          className="my-2"
          label="Gender"
        />
      </Div2Children>
      <PasswordInput
        label="Old password"
        className="my-2"
        value={data.oldPassword}
        onChange={(e) =>
          inputObjStateChange(e, "oldPassword", setData, () => setError(""))
        }
      />
      <Center>
        <MUIButton onClick={save}>Save</MUIButton>
      </Center>
    </Example1Con>
  );
}
