import axios from 'axios';

export const getPostsFunc = ({ page = 1, userUid = '', asAdmin = 0, limit = 10, search = '' } = {}) =>
  axios.get(`/post?page=${page}&userUid=${userUid}&asAdmin=${asAdmin}&limit=${limit}&search=${search}`);

export const createPostFunc = (data, { limit = 10 } = {}) => axios.post(`/post?limit=${limit}`, data);

export const updatePostFunc = (uid, data, { returnPosts = '', returnPost = '', userUid = '', limit = 10 } = {}) =>
  axios.put(`/post/${uid}?returnPosts=${returnPosts}&returnPost=${returnPost}&userUid=${userUid}&limit=${limit}`, data);

export const deletePostFunc = (uid) => axios.delete('/post/' + uid);

export const getCommentsFunc = (postUid) => axios.get('/comments/' + postUid);

export const createCommentFunc = (data) => axios.post('/comment', data);

export const deleteCommentFunc = (uid) => axios.delete('/comment/' + uid);

export const getFriendsPosts = () => axios.get('/friendsposts');

export const getPostFunc = (uid) => axios.get('/post/' + uid);

export const hasPostLikedAxios = (uid) => axios.get('/postliked/' + uid);

export const likePostAxios = (uid) => axios.post('/postlike/' + uid);
