import React, { useEffect, useState } from "react";
import {
  createPostFunc,
  deletePostFunc,
  getFriendsPosts,
  updatePostFunc,
} from "../../../axios/post";
import SinglePost from "../../../components/post/SinglePost";
import EditIcon from "@mui/icons-material/Edit";
import { useSelector } from "react-redux";
import DeleteIcon from "@mui/icons-material/Delete";

export default function Logined() {
  const [posts, setPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);
  const { user } = useSelector((state) => state);

  const modalPostDefaultData = {
    description: "",
    images: [],
  };

  const [modalPostOpen, setModalPostOpen] = useState(false);
  const [modalPostType, setModalPostType] = useState("");
  const [modalPostData, setModalPostData] = useState(modalPostDefaultData);

  const modalPostOnSave = async () => {
    const { description, uid, images } = modalPostData;
    if (!description) return;
    if (modalPostType == "create") {
      const {
        data: { data },
      } = await createPostFunc({
        description,
        images,
      });
      if (data) {
        const { posts, pages } = data;
        setPosts(posts);
      }
    } else if (modalPostType == "update") {
      const {
        data: { data },
      } = await updatePostFunc(uid, {
        description,
        images,
      });

      if (data) {
        const { posts, pages } = data;
        setPosts(posts);
      }
    }

    setModalPostOpen(false);
  };

  const modalPostObj = {
    modalPostOpen,
    setModalPostOpen,
    modalPostType,
    setModalPostType,
    modalPostData,
    setModalPostData,
    modalPostOnSave,
  };

  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [deleteModalType, setDeleteModalType] = useState(false);
  const [deleteModalData, setDeleteModalData] = useState({});

  const deleteModalOnSave = async () => {
    const {
      data: { data },
    } = await deletePostFunc(deleteModalData.uid);
    if (data) {
      const { posts, pages } = data;
      setPosts(posts);
      setDeleteModalOpen(false);
    }
  };

  const deleteModalObj = {
    deleteModalOpen,
    setDeleteModalOpen,
    deleteModalType,
    setDeleteModalType,
    deleteModalData,
    setDeleteModalData,
    deleteModalOnSave,
  };
  useEffect(async () => {
    const {
      data: { data },
    } = await getFriendsPosts();
    setPosts(data);
  }, []);
  return (
    <div>
      {posts.map((a) => (
        <SinglePost
          key={a.uid}
          post={{
            ...a,
            username: a.name,
            image: "/images/user/" + a.userUid + "/" + a.profileImage,
          }}
          // icons={
          //   user.uid == user.uid
          //     ? [
          //         {
          //           icon: (
          //             <EditIcon
          //               style={{
          //                 color: "var(--blue)",
          //               }}
          //             />
          //           ),
          //           onClick: () => {
          //             let { description, images, uid } = a;
          //             images = images.map((a) => a.url);
          //             setModalPostData({ description, uid, images });
          //             setModalPostOpen(true);
          //             setModalPostType("update");
          //           },
          //         },
          //         {
          //           icon: (
          //             <DeleteIcon
          //               style={{ color: "var(--red)" }}
          //               onClick={() => {
          //                 let { description, uid } = a;
          //                 setDeleteModalOpen(true);
          //                 setDeleteModalData({
          //                   uid,
          //                   header: description.slice(0, 30),
          //                 });
          //               }}
          //             />
          //           ),
          //         },
          //       ]
          //     : []
          // }
        />
      ))}
    </div>
  );
}
