import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import SingleChat from './SingleChat';
import { ChatDiv } from './styledComponets';
import io from 'socket.io-client';
import { addChatFunc } from '../../functions/chat';

let socket;
export default function Index() {
  const { chats, user } = useSelector((state) => state);
  const dispatch = useDispatch();
  const ref = useRef('');

  useEffect(() => {
    socket = io();
    socket.emit('join', 'user' + user.uid);
  }, []);

  // useEffect(() => {
  //   socket.on('message', (data) => {
  //     setMessage(data);
  //   });
  // }, [message]);

  useEffect(() => {
    socket.off('openChat');
    socket.on('openChat', (uid) => {
      addChatFunc(dispatch, ref.current, uid);
    });
  }, [dispatch, chats]);

  useEffect(() => {
    ref.current = chats;
  }, [chats]);

  return (
    <ChatDiv>
      {chats.map((chat) => (
        <SingleChat key={chat.uid} socket={socket} chat={chat} />
      ))}
    </ChatDiv>
  );
}
