import * as React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import ListSubheader from "@mui/material/ListSubheader";
import ArticleIcon from "@mui/icons-material/Article";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import { Link } from "react-router-dom";
import { Center, noUnderlineBlack } from "../../../components/styledComponents";
import { queryParams } from "../../../commont tools/url";
import { getUsersAxios } from "../../../axios/users";
import { getPostsFunc } from "../../../axios/post";
import { useLocation } from "react-router-dom";
export default function SwitchListSecondary({
  obj: { setItems, setPages, setPage },
  props,
}) {
  const links = [
    { icon: PeopleAltIcon, title: "People", category: "user" },
    { icon: ArticleIcon, title: "Posts", category: "post" },
  ];
  const { search } = useLocation();
  const q = new URLSearchParams(search);

  const value = q.get("value");
  const category = q.get("category");
  const page = q.get("page");

  React.useEffect(() => {
    setItems([]);
    setPage(1);
  }, [category]);

  React.useEffect(async () => {
    if (category == "user") {
      console.log(page);
      const {
        data: { data },
      } = await getUsersAxios({
        search: value,
        page: page || 1,
        noEmailSearch: "1",
      });
      if (data) {
        const { users, pages } = data;
        setItems(users);
        setPages(pages);
      }
    } else if (category == "post") {
      const {
        data: { data },
      } = await getPostsFunc({ page: page || 1, search: value });
      if (data) {
        const { posts, pages } = data;
        setItems(posts);
        setPages(pages);
      }
    }
  }, [search]);

  return (
    <List
      sx={{ width: "100%", maxWidth: 300, boxShadow: "none!important" }}
      style={{ boxShadow: "none!important" }}
      subheader={<ListSubheader>Categories</ListSubheader>}
    >
      {links.map(({ icon: Icon, title, category: ownCategory }, b) => (
        <ListItem
          key={b}
          style={{
            background: ownCategory == category ? "#f5f5f5" : "inherit",
          }}
        >
          <Link
            style={{ width: "100%", ...noUnderlineBlack }}
            to={`/search?value=${value}&category=${ownCategory}`}
          >
            <Center>
              <ListItemIcon style={{ minWidth: 40 }}>
                <Icon />
              </ListItemIcon>
              <ListItemText id="switch-list-label-wifi" primary={title} />
            </Center>
          </Link>
        </ListItem>
      ))}
    </List>
  );
}
