import React from 'react';
import { beautyDate } from '../../commont tools/beauty';
import ImgLoading from '../imgs/ImgLoading';
import { CommentDiv, CommentImg, CommentMainDiv } from './styledComponent';
import DeleteIcon from '@mui/icons-material/Delete';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { AlignCenter, noUnderlineBlack } from '../styledComponents';

export default function PostComment({
  comment,
  setData,
  data,
  setDeleteModalOpen,
  seteleteModalData,
  userUid: postOwner,
}) {
  const { userUid, profileImage, name, date, title, uid } = comment;
  const onReply = data.commentUid == uid;
  const { user } = useSelector((state) => state);

  return (
    <CommentMainDiv
      style={{
        border: onReply ? '1px solid #e7e7e7' : '',
      }}
    >
      <CommentDiv>
        <Link style={noUnderlineBlack} to={'/profile/' + userUid}>
          <AlignCenter style={{ gap: 10 }}>
            <ImgLoading
              src={`/images/user/${userUid}/${profileImage}`}
              Element={CommentImg}
              noImg="/images/other/noProfileImg.png"
            />
            {name}
          </AlignCenter>
        </Link>
        <span
          className="color-blue pointer"
          onClick={() => setData((state) => ({ ...state, commentUid: onReply ? null : uid }))}
        >
          {onReply ? 'Unreply' : 'Reply'}
        </span>
        <span style={{ fontSize: 13, color: 'darkgrey' }}>{beautyDate(date)}</span>
        {user.uid == userUid || user.uid == postOwner || user.status == 2 ? (
          <DeleteIcon
            className="pointer color-red"
            onClick={() => {
              setDeleteModalOpen(true);
              seteleteModalData(uid);
            }}
          />
        ) : (
          ''
        )}
      </CommentDiv>
      <p className="p-2">{title}</p>
    </CommentMainDiv>
  );
}
