import React from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Fade from '@material-ui/core/Fade';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import IconButton from '@mui/material/IconButton';
import { ThreeDotMenuP } from './styledComponents';

export default function ThreeDotMenu({ items = [], Icon, style }) {
  const myIcon = Icon || (
    <IconButton style={{ padding: 0, ...style }} aria-label="more" aria-controls="long-menu" aria-haspopup="true">
      <MoreHorizIcon />
    </IconButton>
  );

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    event.stopPropagation();
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (event) => {
    event.stopPropagation();
    setAnchorEl(null);
  };

  return (
    <div style={{ position: 'relative', display: 'inline-block' }}>
      <div onClick={handleClick}>{myIcon}</div>
      <Menu id="fade-menu" anchorEl={anchorEl} keepMounted open={open} onClose={handleClose} TransitionComponent={Fade}>
        {items.map(({ title, onClick = () => {} }) => (
          <ThreeDotMenuP
            key={title}
            onClick={(e) => {
              e.stopPropagation();
              onClick(e);
              handleClose(e);
            }}
          >
            {title}
          </ThreeDotMenuP>
          // <MenuItem
          //   onClick={(e) => {
          //     e.stopPropagation();
          //     onClick(e);
          //     handleClose(e);
          //   }}
          // >
          //   {title}
          // </MenuItem>
        ))}
      </Menu>
    </div>
  );
}
