import React, { useState } from "react";
import { Example1Con } from "../styledComponents";
import SignIn from "./SignIn";
import SignUp from "./SignUp";
export default function AuthExample1() {
  const [index, setIndex] = useState(0);
  var maxWidth = 350;
  if (index == 1) maxWidth = 500;
  return (
    <Example1Con style={{ maxWidth }}>
      {
        {
          0: <SignIn setIndex={setIndex} />,
          1: <SignUp setIndex={setIndex} />,
        }[index]
      }
    </Example1Con>
  );
}
