import React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import { Link, useHistory } from 'react-router-dom';
import { Center, FlexColumns, noUnderlineBlack } from '../../../components/styledComponents';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import Pagination from '@mui/material/Pagination';
import { queryParams } from '../../../commont tools/url';
import EmailIcon from '@mui/icons-material/Email';
import CancelScheduleSendIcon from '@mui/icons-material/CancelScheduleSend';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';
import PersonAddAlt1Icon from '@mui/icons-material/PersonAddAlt1';
import PersonRemoveIcon from '@mui/icons-material/PersonRemove';
import {
  addFriendAxios,
  cancelRecieveRequestAxios,
  cancelRequestAxios,
  deleteFriendAxios,
  sendRequestAxios,
} from '../../../axios/users';
import { addPrivateChatFunc } from '../../../functions/chat';
import { useDispatch, useSelector } from 'react-redux';

export default function Users({ obj: { items, pages, page, setPage, setItems } }) {
  let history = useHistory();
  const dispatch = useDispatch();
  const { chats } = useSelector((state) => state);
  const { value, category, page: paramsPage = 1 } = queryParams();
  const message = (uid, name) => {
    addPrivateChatFunc(uid, dispatch, chats, name);
  };

  return (
    <FlexColumns style={{ gap: 20, flex: 1 }}>
      <List
        sx={{ width: '100%', boxShadow: 'none!important' }}
        style={{ boxShadow: 'none!important' }}
        subheader={<ListSubheader>Users</ListSubheader>}
      >
        {items.map(({ uid, fName, lName, friend, sendedRequest, toRequest, me }, b) => (
          <ListItem key={uid + fName + lName}>
            <Link style={{ width: '100%', ...noUnderlineBlack }} to={`/profile/${uid}`}>
              <Center>
                <ListItemIcon style={{ minWidth: 40 }}>
                  <PeopleAltIcon />
                </ListItemIcon>
                <ListItemText id="switch-list-label-wifi" primary={fName + ' ' + lName} />
              </Center>
            </Link>
            {friend && (
              <>
                <EmailIcon
                  onClick={() => message(uid, fName + ' ' + lName)}
                  titleAccess="Message"
                  className="pointer"
                />
                <PersonRemoveIcon
                  onClick={async () => {
                    deleteFriendAxios(uid);
                    const updatedItems = [...items];
                    delete updatedItems.find((a) => a.uid === uid).friend;
                    setItems(updatedItems);
                  }}
                  titleAccess="Message"
                  className="pointer"
                />
              </>
            )}
            {sendedRequest && (
              <CancelScheduleSendIcon
                titleAccess="Cancel friend request"
                onClick={async () => {
                  cancelRequestAxios(uid);
                  const updatedItems = [...items];
                  delete updatedItems.find((a) => a.uid === uid).sendedRequest;
                  setItems(updatedItems);
                }}
                className="pointer"
              />
            )}
            {toRequest && (
              <>
                <CheckCircleIcon
                  titleAccess="Accept friend request"
                  className="pointer"
                  onClick={async () => {
                    const {
                      data: { data, error },
                    } = await addFriendAxios(uid);
                    if (error) alert(error);
                    else if (data) {
                      const updatedItems = [...items];
                      const current = updatedItems.find((a) => a.uid === uid);

                      delete current.toRequest;
                      current.friend = true;

                      setItems(updatedItems);
                    }
                  }}
                />
                <CancelIcon
                  titleAccess="Reject friend request"
                  className="pointer"
                  onClick={async () => {
                    cancelRecieveRequestAxios(uid);

                    const updatedItems = [...items];
                    const current = updatedItems.find((a) => a.uid === uid);

                    delete current.toRequest;

                    setItems(updatedItems);
                  }}
                />
              </>
            )}
            {!(friend || sendedRequest || toRequest || me) && (
              <PersonAddAlt1Icon
                titleAccess="Send friend request"
                onClick={async () => {
                  const {
                    data: { data, error },
                  } = await sendRequestAxios(uid);
                  if (error) alert(error);
                  if (data) {
                    const updatedItems = [...items];
                    updatedItems.find((a) => a.uid === uid).sendedRequest = true;
                    setItems(updatedItems);
                  }
                }}
                className="pointer"
              />
            )}
          </ListItem>
        ))}
      </List>
      <Pagination
        count={pages}
        page={page}
        onChange={(e, page) => {
          setPage(page);
          const pageurl = page > 1 ? `&page=${page}` : '';
          history.push(`/search?value=${value}&category=${category}` + pageurl);
        }}
      />
    </FlexColumns>
  );
}
