import React from 'react';
import { useSelector } from 'react-redux';
import { addFriendByUidAxios, cancelRecieveRequestByUidAxios, cancelRequestByUidAxios } from '../../../axios/users';
import ImgLoading from '../../imgs/ImgLoading';
import { AlignCenter, FlexColumns, MUIButton, NotificationProfileImg } from '../../styledComponents';

export default function RequestTo({ item, setNotifications }) {
  console.log(item);
  return (
    <AlignCenter>
      <ImgLoading
        loadingHeight="30"
        loadingWidth="30"
        Element={NotificationProfileImg}
        src={'/images/user/' + item.image}
      />
      <FlexColumns className="ml-2">
        <div>Sended request from {item.username}</div>
        <div>
          <MUIButton
            className="mt-1 p-1 bg-red"
            onClick={() => {
              cancelRecieveRequestByUidAxios(item.valueUid);
              setNotifications((state) => state.filter((b) => b.uid !== item.uid));
            }}
          >
            Cancel
          </MUIButton>
          <MUIButton
            className="mt-1 ml-1 p-1"
            onClick={() => {
              addFriendByUidAxios(item.valueUid);
              setNotifications((state) => state.filter((b) => b.uid !== item.uid));
            }}
          >
            Accept
          </MUIButton>
        </div>
      </FlexColumns>
    </AlignCenter>
  );
}
