import { TextField } from "@material-ui/core";
import React, { useState } from "react";
import { inputObjStateChange } from "../../commont tools/other";
import SimpleModal from "../SimpleModal";
import TextareaAutosize from "@mui/material/TextareaAutosize";
import Album from "../Album";
import ImgLoading from "../imgs/ImgLoading";
import { useSelector } from "react-redux";
import {
  deleteIconStyle,
  Img,
  ImgContainer,
  moveIconStyle,
} from "./styledComponent";
import DeleteIcon from "@mui/icons-material/Delete";
import {
  SortableContainer,
  SortableElement,
  SortableHandle,
} from "react-sortable-hoc";
import { arrayMoveImmutable as arrayMove } from "array-move";
import MoveUpIcon from "@mui/icons-material/MoveUp";

export default function PostModal({
  modalPostObj: {
    modalPostOpen,
    setModalPostOpen,
    modalPostType,
    setModalPostType,
    modalPostData,
    setModalPostData,
    modalPostOnSave,
  },
}) {
  const headers = {
    create: "Create post",
  };
  const { user } = useSelector((state) => state);

  const [albumOpen, setAlbumOpen] = useState(false);

  const onChooseImage = (e) => {
    setModalPostData((state) => ({
      ...state,
      images: [...state.images, user.uid + "/" + e],
    }));
  };

  const onSortEnd = ({ oldIndex, newIndex }) => {
    const newItems = arrayMove(modalPostData.images, oldIndex, newIndex);
    setModalPostData((state) => ({ ...state, images: newItems }));
  };

  const RowHandler = SortableHandle(() => <MoveUpIcon style={moveIconStyle} />);

  const SortableItem = SortableElement(({ value, indexx }) => {
    return (
      <div style={{ position: "relative", zIndex: 9999 }}>
        <div className="relative">
          <RowHandler />
          <DeleteIcon
            style={deleteIconStyle}
            onClick={() => {
              const newImages = [...modalPostData.images].filter(
                (a, b) => b != indexx
              );
              setModalPostData((state) => ({ ...state, images: newImages }));
            }}
          />
          <Img src={"/images/user/" + value} />
        </div>
      </div>
    );
  });

  const SortableList = SortableContainer(({ items }) => {
    return (
      <ImgContainer>
        {items.map((value, index) => (
          <SortableItem
            key={`item-${index}`}
            index={index}
            value={value}
            indexx={index}
          />
        ))}
      </ImgContainer>
    );
  });

  return (
    <SimpleModal
      open={modalPostOpen}
      setOpen={setModalPostOpen}
      header={headers[modalPostType]}
      maxWidth="md"
      onSave={modalPostOnSave}
    >
      <TextareaAutosize
        aria-label="minimum height"
        value={modalPostData.description}
        onChange={(e) =>
          inputObjStateChange(e, "description", setModalPostData)
        }
        minRows={3}
        placeholder="Description"
        style={{
          minHeight: 50,
          maxHeight: 300,
          overflow: "auto!important",
          font: "inherit",
          color: "currentcolor",
          width: "100%",
          margin: "0px",
          display: "block",
          minWidth: "0px",
          background: "none",
          animationName: "mui-auto-fill-cancel",
          letterSpacing: "inherit",
          animationDuration: "10ms",
          border: "1px solid rgb(196, 196, 196)",
          borderRadius: "3px",
          padding: "10px 14px",
          boxSizing: "border-box",
          resize: "vertical",
        }}
      />
      <p className="my-2 pointer" onClick={() => setAlbumOpen(true)}>
        Open album
      </p>
      <Album
        open={albumOpen}
        setOpen={setAlbumOpen}
        onChooseImage={onChooseImage}
      />
      <SortableList
        useDragHandle={true}
        axis="xy"
        items={modalPostData.images}
        onSortEnd={onSortEnd}
      />
    </SimpleModal>
  );
}
