import React, { useEffect, useState } from 'react';
import Loading from '../Loading';

export default function ImgLoading({
  src = '',
  Element,
  loadingHeight,
  loadingWidth,
  noImg = '/images/other/noimage.png',
  loadingStyle = {},
}) {
  const [loaded, setLoaded] = useState(false);

  function testImage(URL) {
    var tester = new Image();
    tester.onload = imageFound;
    tester.onerror = imageNotFound;
    tester.src = URL;
  }

  function imageFound() {
    setLoaded(true);
  }

  function imageNotFound() {
    setLoaded('error');
  }
  useEffect(() => {
    testImage(src);
  }, [src]);
  return (
    <>
      {loaded ? (
        <>
          {!Element ? (
            <img src={loaded == 'error' ? noImg : src} style={{ height: +loadingHeight, width: +loadingWidth }} />
          ) : (
            <Element src={loaded == 'error' ? noImg : src} />
          )}
        </>
      ) : (
        <Loading height={loadingHeight} width={loadingWidth} style={loadingStyle} />
      )}
    </>
  );
}
