import React, { useState } from "react";
import { Example1Con } from "../styledComponents";
import TextField from "@mui/material/TextField";
import PasswordInput from "../../inputs/PasswordInput";
import Button from "@mui/material/Button";
import { Center } from "../../styledComponents";
import { inputObjStateChange } from "../../../commont tools/other";
import { signInFunc } from "../../../functions/auth";
import { useDispatch } from "react-redux";

export default function AuthExample1({ setIndex }) {
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    email: "",
    password: "",
  });
  const [error, setError] = useState("");
  const signIn = async () => {
    const { error } = await signInFunc(dispatch, form);
    if (error) setError(error);
  };
  return (
    <>
      <p className="mb-2" style={{ color: "var(--red)" }}>
        {error}
      </p>
      <TextField
        id="outlined-basic"
        label="Email"
        variant="outlined"
        size="small"
        fullWidth
        className="my-2"
        value={form.email}
        onChange={(e) => inputObjStateChange(e, "email", setForm)}
      />
      <PasswordInput
        className="my-2"
        value={form.password}
        onChange={(e) => inputObjStateChange(e, "password", setForm)}
      />
      <span className="mb-2 pointer block" onClick={() => setIndex(1)}>
        Don't have an account?
      </span>
      <Center>
        <Button variant="contained" onClick={signIn}>
          Sign in
        </Button>
      </Center>
    </>
  );
}
