const initialState = {
  user: false,
  chats: [],
  navbarMessages: [],
};

const reducer = (state = initialState, { payload, type }) => {
  switch (type) {
    case 'setUser':
      return { ...state, user: payload };
    case 'ADD_CHAT':
      return { ...state, chats: [payload, ...state.chats] };
    case 'REMOVE_CHATS':
      return { ...state, chats: [] };
    case 'CLOSE_CHAT':
      return { ...state, chats: state.chats.filter((a) => a.uid != payload) };
    case 'MINIMIZE_CHAT':
      return { ...state, chats: state.chats.map((a) => (a.uid != payload ? a : { ...a, open: !a.open })) };
    case 'UPDATE_NAVBAR_MESSAGES':
      return { ...state, navbarMessages: payload };
    default:
      return state;
  }
};

export default reducer;
