import { TextField } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { createRoomAxios, updateRoomAxios } from '../../axios/chat';
import { getMyFriendsAxios } from '../../axios/users';
import { inputObjStateChange, objStateChange } from '../../commont tools/other';
import ComboCheckbox from '../selects/ComboCheckbox';
import SimpleModal from '../SimpleModal';
import { FlexColumns } from '../styledComponents';

export default function RoomModal({
  additionalData = [],
  data,
  setData,
  open,
  setOpen,
  type,
  onSave: onSaveFunc,
  defaultValue = () => {},
  ...props
}) {
  const { user } = useSelector((a) => a);
  const [friends, setFriends] = useState([]);
  const getMyFriends = async () => {
    const {
      data: { data },
    } = await getMyFriendsAxios();
    const friendsData = data
      .map((a) => ({ value: user.uid === a.user1 ? a.user2 : a.user1, name: a.name }))
      .filter((a) => !additionalData.find((b) => b.value === a.value));

    const newFriends = [...friendsData, ...additionalData];
    setFriends(newFriends);
    console.log();
    objStateChange(defaultValue(newFriends) || [], 'users', setData);
  };
  useEffect(() => {
    console.log(open);
    if (!open) return;
    getMyFriends();
  }, [open]);

  console.log(data);

  const onSave = async () => {
    let { name, users, uid } = data;
    if (!(name && users.length)) return alert('Please fill all the fields');
    users = users.map((a) => a.value);
    const newData = { name, users };

    if (type === 'create') {
      await createRoomAxios(newData);
      onSaveFunc({ name, users, friends, type });
      setOpen(false);
    } else if (type === 'update') {
      await updateRoomAxios(uid, newData);
      onSaveFunc({ name, users, friends, type });
      setOpen(false);
    }
  };
  if (!friends) return '';
  // console.log(friends, defaultValue(friends));
  return (
    <SimpleModal open={open} setOpen={setOpen} onSave={onSave} {...props}>
      <FlexColumns className="gap-2">
        <TextField
          id="outlined-basic"
          label="Room name"
          variant="outlined"
          size="small"
          fullWidth
          value={data.name}
          onChange={(e) => inputObjStateChange(e, 'name', setData)}
        />
        <ComboCheckbox
          size="small"
          value={data.users}
          items={friends}
          placeholder="Your friends"
          onChange={(e) => {
            console.log(e, 123);
            objStateChange(e, 'users', setData);
          }}
        />
      </FlexColumns>
    </SimpleModal>
  );
}
