import ProtectedRoute from '../routeTypes/onAuth';

import Search from '../pages/onAuth/search';
import User from '../pages/onAuth/user';
import Post from '../pages/onAuth/post';
import Settings from '../pages/onAuth/settings';
import { Fragment } from 'react';

const routes = [
  <ProtectedRoute exact path="/profile/:uid" component={User} />,
  <ProtectedRoute exact path="/search" component={Search} />,
  <ProtectedRoute exact path="/post/:uid" component={Post} />,
  <ProtectedRoute exact path="/settings" component={Settings} />,
];

export default routes;
