import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  addFriendAxios,
  cancelRequestAxios,
  connectionToUser,
  deleteFriendAxios,
  getUserAxios,
  sendRequestAxios,
  updateUserAxios,
} from '../../../axios/users';
import ImgLoading from '../../../components/imgs/ImgLoading';
import { FlexColumns, Flex, JustifySpaceBetweenAlignCenter, AlignCenter } from '../../../components/styledComponents';
import { BgImage, ChangeMyAccountImg, Container, InnerContainer, ProfileImage } from './styledComponent';
import EmailIcon from '@mui/icons-material/Email';
import PersonRemoveIcon from '@mui/icons-material/PersonRemove';
import PersonAddAlt1Icon from '@mui/icons-material/PersonAddAlt1';
import CancelScheduleSendIcon from '@mui/icons-material/CancelScheduleSend';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Button } from '@material-ui/core';
import PostModal from '../../../components/post/PostModal';
import { createPostFunc, deletePostFunc, getPostsFunc, updatePostFunc } from '../../../axios/post';
import SinglePost from '../../../components/post/SinglePost';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import DeleteModal from '../../../components/post/DeleteModal';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import Album from '../../../components/Album';
import { useParams } from 'react-router';
import { addChatFunc, addPrivateChatFunc } from '../../../functions/chat';

export default function Index() {
  const { uid } = useParams();
  const { user, chats } = useSelector((state) => state);
  const dispatch = useDispatch();

  const [currUser, setCurrUser] = useState(false);
  const [connection, setConnection] = useState('');
  const [page, setPage] = useState(1);
  const [posts, setPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const [openAlbum, setOpenAlbum] = useState(false);
  const [openAlbumType, setOpenAlbumType] = useState(null);

  const albumChooseImage = async (e) => {
    if (openAlbumType == 'profileImg') {
      console.log(e);
      const {
        data: { data },
      } = await updateUserAxios(uid, {
        profileImage: e,
      });
      setCurrUser((state) => ({ ...state, ...data }));
    } else if (openAlbumType == 'backgroundImg') {
      const {
        data: { data },
      } = await updateUserAxios(uid, {
        backgroundImage: currUser.uid + '/' + e,
      });
      setCurrUser((state) => ({ ...state, ...data }));
    }
  };

  const fetchUser = async () => {
    const {
      data: { data },
    } = await getUserAxios(uid);
    if (data) {
      setCurrUser(data);
      if (data.uid == user.uid) setConnection('me');
      else {
        const { data: connection } = await connectionToUser(data.uid);
        setConnection(connection);
      }
    } else setCurrUser(null);
  };

  const fetchPosts = async (userUid) => {
    setLoaded(false);
    const {
      data: { data },
    } = await getPostsFunc({ page, userUid, limit: 10 });
    if (data) {
      const { posts, pages } = data;

      setPosts((state) => [...state, ...posts]);
      setLoaded(true);
    }
  };

  const modalPostDefaultData = {
    description: '',
    images: [],
  };

  const [modalPostOpen, setModalPostOpen] = useState(false);
  const [modalPostType, setModalPostType] = useState('');
  const [modalPostData, setModalPostData] = useState(modalPostDefaultData);

  const modalPostOnSave = async () => {
    setLoaded(false);
    const { description, uid, images } = modalPostData;
    if (!description) return;
    if (modalPostType == 'create') {
      const {
        data: { data },
      } = await createPostFunc(
        {
          description,
          images,
        },
        { limit: page * 10 + 1 },
      );
      if (data) {
        const { posts, pages } = data;
        setPosts(posts);
      }
    } else if (modalPostType == 'update') {
      const {
        data: { data },
      } = await updatePostFunc(
        uid,
        {
          description,
          images,
        },
        { returnPosts: 1, userUid: currUser.uid, limit: page * 10 },
      );

      if (data) {
        const { posts, pages } = data;
        setPosts(posts);
      }
    }

    setModalPostOpen(false);
  };

  const modalPostObj = {
    modalPostOpen,
    setModalPostOpen,
    modalPostType,
    setModalPostType,
    modalPostData,
    setModalPostData,
    modalPostOnSave,
  };

  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [deleteModalType, setDeleteModalType] = useState(false);
  const [deleteModalData, setDeleteModalData] = useState({});

  const deleteModalOnSave = async () => {
    const {
      data: { data },
    } = await deletePostFunc(deleteModalData.uid);
    if (data) {
      const { posts, pages } = data;
      setPosts(posts);
      setDeleteModalOpen(false);
    }
  };

  const deleteModalObj = {
    deleteModalOpen,
    setDeleteModalOpen,
    deleteModalType,
    setDeleteModalType,
    deleteModalData,
    setDeleteModalData,
    deleteModalOnSave,
  };

  window.onscroll = (a) => {
    var body = document.body,
      html = document.documentElement;

    var height = Math.max(
      body.scrollHeight,
      body.offsetHeight,
      html.clientHeight,
      html.scrollHeight,
      html.offsetHeight,
    );

    if (height - 100 <= window.scrollY + window.innerHeight) {
      if (!loaded) return;
      setPage((old) => old + 1);
    }
  };

  const message = () => {
    // addChatFunc(dispatch, chats, uid, currUser);
    addPrivateChatFunc(uid, dispatch, chats, currUser.fName + ' ' + currUser.lName);
  };

  useEffect(async () => {
    setPosts([]);
    setPage(1);
    fetchUser();
  }, [uid, user]);

  useEffect(async () => {
    await fetchPosts(uid);
  }, [page, uid]);

  useEffect(() => {
    setLoaded(true);
  });

  if (currUser == null) return 'Page is not avaible';
  if (currUser == false) return 'Loading';
  return (
    <div style={{ marginTop: -9 }}>
      <BgImage
        style={{
          backgroundImage: `url(/images/user/${currUser.backgroundImage})`,
        }}
      >
        {connection == 'me' && (
          <CameraAltIcon
            onClick={() => {
              setOpenAlbumType('backgroundImg');
              setOpenAlbum(true);
            }}
          />
        )}
      </BgImage>
      <Container>
        <Album open={openAlbum} setOpen={setOpenAlbum} onChooseImage={albumChooseImage} />
        <InnerContainer>
          <JustifySpaceBetweenAlignCenter>
            <Flex>
              <ChangeMyAccountImg>
                {connection == 'me' && (
                  <CameraAltIcon
                    onClick={() => {
                      setOpenAlbumType('profileImg');
                      setOpenAlbum(true);
                    }}
                  />
                )}
                <ImgLoading
                  noImg="/images/other/noProfileImg.png"
                  Element={ProfileImage}
                  src={'/images/user/' + currUser.uid + '/' + currUser.profileImage}
                  loadingHeight={200}
                  loadingWidth={200}
                  loadingStyle={{ marginTop: -50 }}
                />
              </ChangeMyAccountImg>
              <AlignCenter style={{ marginLeft: 20 }}>
                <FlexColumns style={{ gap: 10 }}>
                  <h2>{currUser.fName + ' ' + currUser.lName}</h2>
                  <p>
                    {currUser.friends} {currUser.friends > 1 ? 'Friends' : 'Friend'}
                  </p>
                </FlexColumns>
              </AlignCenter>
            </Flex>
            <span>
              {
                {
                  me: (
                    <Button
                      variant="contained"
                      onClick={() => {
                        setModalPostData(modalPostDefaultData);
                        setModalPostOpen(true);
                        setModalPostType('create');
                      }}
                      style={{ backgroundColor: 'var(--blue)', color: 'white', position: 'initial' }}
                    >
                      Add post
                    </Button>
                  ),
                  Friends: (
                    <>
                      <EmailIcon onClick={message} titleAccess="Message" className="pointer" />
                      <PersonRemoveIcon
                        onClick={async () => {
                          deleteFriendAxios(uid);
                          setConnection('No one');
                        }}
                        titleAccess="Message"
                        className="pointer"
                      />
                    </>
                  ),
                  'No one': (
                    <PersonAddAlt1Icon
                      titleAccess="Send friend request"
                      onClick={async () => {
                        const {
                          data: { data, error },
                        } = await sendRequestAxios(uid);
                        if (error) alert(error);
                        if (data) setConnection('Sender');
                      }}
                      className="pointer"
                    />
                  ),
                  Sender: (
                    <CancelScheduleSendIcon
                      titleAccess="Cancel friend request"
                      onClick={async () => {
                        cancelRequestAxios(uid);
                        setConnection('No one');
                      }}
                      className="pointer"
                    />
                  ),
                  Reciever: (
                    <CheckCircleIcon
                      titleAccess="Accept friend request"
                      className="pointer"
                      onClick={async () => {
                        const {
                          data: { data, error },
                        } = await addFriendAxios(uid);
                        if (error) alert(error);
                        else if (data) setConnection('Friends');
                      }}
                    />
                  ),
                }[connection]
              }
            </span>
          </JustifySpaceBetweenAlignCenter>
        </InnerContainer>
        {posts.map((a) => (
          <SinglePost
            key={a.uid}
            post={{
              ...a,
              username: currUser.fName + ' ' + currUser.lName,
              image: '/images/user/' + currUser.uid + '/' + currUser.profileImage,
            }}
            icons={
              user.uid == currUser.uid || user.status == 2
                ? [
                    {
                      icon: (
                        <EditIcon
                          style={{
                            color: 'var(--blue)',
                          }}
                        />
                      ),
                      onClick: () => {
                        let { description, images, uid } = a;
                        images = images.map((a) => a.url);
                        setModalPostData({ description, uid, images });
                        setModalPostOpen(true);
                        setModalPostType('update');
                      },
                    },
                    {
                      icon: (
                        <DeleteIcon
                          style={{ color: 'var(--red)' }}
                          onClick={() => {
                            let { description, uid } = a;
                            setDeleteModalOpen(true);
                            setDeleteModalData({
                              uid,
                              header: description.slice(0, 30),
                            });
                          }}
                        />
                      ),
                    },
                  ]
                : []
            }
          />
        ))}
        <PostModal modalPostObj={modalPostObj} />
        <DeleteModal deleteModalObj={deleteModalObj} />
      </Container>
    </div>
  );
}
