import React, { useEffect, useRef, useState } from 'react';
import { AlignCenter, Flex, JustifySpaceBetweenAlignCenter, MUIButton } from '../styledComponents';
import { Chat, ChatImage, ChatMessage, ChatMesssages, ChatName } from './styledComponets';
import TextField from '@mui/material/TextField';
import { getUserAxios } from '../../axios/users';
import { getChatDataAxios, getChatMessagesAxios, seenChatAxios, sendChatMessageAxios } from '../../axios/chat';
import { useDispatch, useSelector } from 'react-redux';
import CloseIcon from '@mui/icons-material/Close';
import AspectRatioIcon from '@mui/icons-material/AspectRatio';
import { closeChatFunc, minimizeChatFunc, updateNavbarMessages } from '../../functions/chat';
import ImgLoading from '../imgs/ImgLoading';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import RoomModal from './RoomModal';
import DeleteRoomModal from './DeleteRoomModal';

export default function SingleChat({ chat: { uid, open }, socket }) {
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [loaded, setLoaded] = useState(false);
  const [users, setUsers] = useState([]);
  const { user, navbarMessages } = useSelector((a) => a);
  const [chatName, setChatName] = useState('');
  const [isRoom, setIsRoom] = useState(false);
  const dispatch = useDispatch();
  const defaultModalData = { name: '', users: [] };
  const [roomModalOpen, setRoomModalOpen] = useState(false);
  const [roomModalData, setRoomModalData] = useState(defaultModalData);
  const [roomModalDefaultValue, setRoomModalDefaultValue] = useState([]);

  const [deleteRoomOpen, setDeleteRoomOpen] = useState(false);
  const [deleteRoomData, setDeleteRoomData] = useState({});
  const deleteRoomOnSave = () => {
    updateNavbarMessages(
      dispatch,
      navbarMessages.filter((a) => a.uid !== uid),
    );

    socket.emit('disconnectchat', uid);
    closeChatFunc(dispatch, uid);
  };

  const messageRef = useRef();
  const send = () => {
    if (!message) return;
    messageRef.current = { uid, message };
    socket.emit('sendMessage', { uid, message, from: user.uid });
    setMessages([...messages, { from: user.uid, message, username: '', date: new Date() - 1, uid: Math.random() }]);
    setMessage('');
    sendChatMessageAxios(uid, { message });
  };

  const messagePosition = {
    true: { display: 'flex', justifyContent: ' flex-end', textAlign: 'right', marginLeft: 'auto' },
    false: {},
  };
  const messageBg = {
    true: { backgroundColor: 'var(--blue)', color: 'white' },
    false: {},
  };

  const getMessages = async (params = {}) => {
    const {
      data: { data },
    } = await getChatMessagesAxios(uid, params);
    if (data) {
      const { users, messages, chat } = data;
      if (users && chat) {
        setUsers(users);

        let currChatName = '';
        if (chat.name) {
          setIsRoom(true);
          currChatName = chat.name;
        } else {
          const { username } = users.filter((a) => a.userUid !== user.uid)[0];
          currChatName = username;
        }

        setChatName(currChatName);
      }
      if (messages) {
        const oldMessages = messages.map(({ message, date, chatuserUid, uid, username, image, notification }) => ({
          message,
          date,
          from: users.find(({ uid }) => uid == chatuserUid)?.userUid,
          uid,
          username,
          image,
          notification,
        }));
        setMessages(oldMessages);
      }
    }
    setLoaded(true);
  };

  const seenChat = async () => {
    seenChatAxios(uid);
  };

  useEffect(() => {
    socket.emit('join', uid);
  }, []);

  useEffect(() => {
    getMessages();
    seenChat();
  }, []);

  useEffect(() => {
    socket.on('message', (data) => {
      setMessages((state) => [...state, { ...data, uid: Math.random() }]);
      seenChat();
    });
  }, []);

  useEffect(() => {
    ref.current?.scrollTo(0, ref.current.scrollHeight);
  }, [messages]);

  const ref = useRef();
  return (
    <Chat>
      {roomModalOpen && (
        <RoomModal
          additionalData={users
            .filter((a) => a.userUid !== user.uid)
            .map((a) => ({ value: a.userUid, name: a.username }))}
          open={roomModalOpen}
          setOpen={setRoomModalOpen}
          data={roomModalData}
          setData={setRoomModalData}
          type={'update'}
          onSave={({ name, users: newUsers, friends, type }) => {
            if (chatName !== name) {
              const message = 'changed room name to ' + name;
              const newmessage = {
                uid,
                message: message,
                from: user.uid,
                notification: 1,
              };
              socket.emit('sendMessage', newmessage);

              sendChatMessageAxios(uid, newmessage);
              setMessages([...messages, { message: message, notification: 1, uid: Math.random(), from: user.uid }]);
            }

            const chatUsers = users.filter((a) => a.userUid !== user.uid && a.inChat === 1);
            const newMessages = [];

            for (let k of newUsers.filter((a) => !chatUsers.find((b) => b.userUid === a))) {
              const name = friends.find((a) => a.value === k)?.name;
              const message = 'added user ' + name;
              const newmessage = {
                uid,
                message,
                from: user.uid,
                notification: 1,
              };
              socket.emit('sendMessage', newmessage);
              newMessages.push({
                from: user.uid,
                message,
                username: '',
                date: new Date() - 1,
                uid: Math.random(),
                notification: 1,
              });
            }

            for (let k of chatUsers.filter((a) => !newUsers.find((b) => a.userUid === b))) {
              const name = friends.find((a) => a.value === k.userUid)?.name;
              const message = 'deleted user ' + name;
              const newmessage = {
                uid,
                message,
                from: user.uid,
                notification: 1,
              };
              socket.emit('sendMessage', newmessage);
              newMessages.push({
                from: user.uid,
                message,
                username: '',
                date: new Date() - 1,
                uid: Math.random(),
                notification: 1,
              });
            }

            if (newMessages.length) setMessages([...messages, ...newMessages]);

            getMessages({ chat: 1, messages: '' });
          }}
          defaultValue={(allData) => allData.filter((a) => roomModalDefaultValue.includes(a.value))}
        />
      )}
      <DeleteRoomModal
        open={deleteRoomOpen}
        setOpen={setDeleteRoomOpen}
        data={deleteRoomData}
        onSave={deleteRoomOnSave}
      />
      <ChatName className="px-3 py-2">
        <JustifySpaceBetweenAlignCenter>
          <span>{chatName}</span>
          <div>
            {isRoom && (
              <>
                <DeleteIcon
                  onClick={() => {
                    setDeleteRoomOpen(true);
                    setDeleteRoomData({ roomName: chatName, uid });
                  }}
                  className="pointer"
                />
                <EditIcon
                  onClick={() => {
                    setRoomModalDefaultValue(
                      users.filter((a) => a.userUid !== user.uid && a.inChat === 1).map((a) => a.userUid),
                    );
                    setRoomModalData({
                      name: chatName,
                      users: users.filter((a) => a.userUid !== user.uid).map((a) => a.userUid),
                      uid,
                    });
                    setRoomModalOpen(true);
                  }}
                  className="pointer"
                />
              </>
            )}
            <AspectRatioIcon
              className="pointer"
              onClick={() => {
                minimizeChatFunc(dispatch, uid);
              }}
            />
            <CloseIcon
              className="pointer"
              onClick={() => {
                socket.emit('disconnectchat', uid);
                closeChatFunc(dispatch, uid);
              }}
            />
          </div>
        </JustifySpaceBetweenAlignCenter>
      </ChatName>
      {open && (
        <>
          <ChatMesssages className="p-3" ref={ref}>
            {!loaded && 'Loading'}
            {messages.map((a) => {
              const own = user.uid === a.from;

              const { username, profileImage } = users.find((b) => b.userUid === a.from);
              a.username = user.uid === a.from ? 'You' : username;
              a.image = a.from + '/' + profileImage;

              if (a.notification)
                return (
                  <p
                    style={{
                      fontSize: '13px',
                      textAlign: 'center',
                      margin: '5px 0',
                      color: '#b8b8b8',
                    }}
                  >
                    {a.username} {a.message}
                  </p>
                );

              return (
                <AlignCenter key={a.uid}>
                  {!own && (
                    <ImgLoading
                      Element={ChatImage}
                      loadingWidth="35"
                      loadingHeight="35"
                      src={'/images/user/' + a.image}
                    />
                  )}
                  <div style={{ ...messagePosition[own], maxWidth: '50%' }}>
                    {!own && <p className="mb-2">{a.username}</p>}
                    <ChatMessage style={messageBg[own]}>{a.message}</ChatMessage>
                  </div>
                </AlignCenter>
              );
            })}
          </ChatMesssages>
          <form
            onSubmit={(e) => {
              e.preventDefault();
              send();
            }}
          >
            <Flex className="gap-1 p-3">
              <TextField
                id="outlined-basic"
                label="Message"
                variant="outlined"
                size="small"
                fullWidth
                style={{ flex: 1 }}
                value={message}
                onChange={(e) => setMessage(e.target.value)}
              />
              <MUIButton style={{ padding: 4, fontSize: '13px' }}>Send</MUIButton>
            </Flex>
          </form>
        </>
      )}
    </Chat>
  );
}
